.experience_container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;

    >div {
        background: var(--color-bg-variant);
        padding: 2.4rem 5rem;
        border-radius: 2rem;
        border: 1px solid transparent;
        transition: var(--transition);

        &:hover {
            background: transparent;
            border-color: var(--color-primary-variant);
            cursor: default;
        }

        h3 {
            text-align: center;
            margin-bottom: 2rem;
            color: var(--color-primary);
        }
    }

    .experience_content {
        display: grid;
        grid-template-columns: 1fr 1fr;
        row-gap: 2rem;

        .experience_details {
            display: flex;
            gap: 1rem;

            .experience_details_icon {
                margin-top: 5px;
                color: var(--color-primary);
            }
        }
    }

    @media (max-width: 1024px) {
        grid-template-columns: 1fr;

        >div {
            width: 80%;
            padding: 2rem;
            margin: 0 auto;
        }

        .experience_content {
            padding: 1rem;
        }
    }

    @media (max-width: 600px) {
        gap: 1rem;

        >div {
            width: 100%;
            padding: 2rem 1rem;
        }
    }
}