.portfolio_container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2.5rem;

    .portfolio_item {
        background: var(--color-bg-variant);
        padding: 1.3rem;
        border-radius: 2rem;
        border: 1px solid transparent;
        transition: var(--transition);

        &:hover {
            border-color: var(--color-primary-variant);
            background: transparent;
        }

        .portfolio_item_image {

            border-radius: 1.5rem;
            overflow: hidden;
            position: relative;
            height: 500px;
            width: 100%;

            .bg {
                margin: 0 auto;
                background-size: cover;
                background-position: center;
                filter: saturate(300%) blur(2px) brightness(70%);
                width: 100%;
                height: 100%;
            }


            img {
                height: 100%;
                width: 100%;
                position: absolute;
                top: 0;
                object-fit: contain;
            }

            .item_info {
                position: absolute;
                bottom: 0.6rem;
                right: 0.6rem;
                font-size: 1.3rem;
                color: var(--color-light);
            }
        }

        h3 {
            margin: 1.2rem 0 2rem;
        }

        .portfolio_item_cta {
            display: flex;
            gap: 1rem;
            margin-bottom: 1rem;
        }
    }

    //===================== MQ (MEDIUM) =====================

    @media (max-width: 1024px) {
        gap: 1.2rem;
    }

    //===================== MQ (SMALL) =====================

    @media (max-width: 800px) {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
}