.about_container {


    grid-template-columns: 35% 50%;
    gap: 15%;

    .about_me {
        width: 100%;
        aspect-ratio: 1;
        border-radius: 2rem;
        background: linear-gradient(45deg, transparent, var(--color-primary), transparent);

        .about_me_image {
            width: 100%;
            height: 100%;
            border-radius: 2rem;
            overflow: hidden;
            transform: rotate(10deg);
            transition: var(--transition);

            &:hover {
                transform: rotate(0);
            }
        }
    }

    .about_content {
        p {
            margin: 2rem 0 2.6rem;
            color: var(--color-light);
        }
    }

    //===================== MQ (MEDIUM) =====================

    @media (max-width: 1024px) {
        grid-template-columns: 1fr;
        gap: 0;

        .about_me {
            width: 50%;
            margin: 2rem auto 4rem;
        }

        .about_content {
            p {
                margin: 1rem 0 1.5rem;
            }
        }
    }

    //===================== MQ (SMALL) =====================

    @media (max-width: 600px) {
        .about_me {
            width: 65%;
            margin: 0 auto 3rem;
        }

        .about_cards {
            grid-template-columns: repeat(10, 1fr);
            gap: 1rem;

        }

        .about_card {
            &:nth-child(1) {
                grid-column: 1 / span 5;
            }

            &:nth-child(2) {
                grid-column: 6 / span 5;
            }

            &:nth-child(3) {
                grid-column: 3 / span 6;
            }

        }

        .about_content {
            text-align: center;

            p {
                margin: 1.5rem 0;
            }
        }
    }
}

.about_cards {
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;

    .about_card {
        // height: min-content;
        background: var(--color-bg-variant);
        border: 1px solid transparent;
        border-radius: 1rem;
        padding: 1rem;
        text-align: center;
        transition: var(--transition);
        // aspect-ratio: 1/1;

        &:hover {
            background: transparent;
            border-color: var(--color-primary-variant);
            cursor: default;
        }

        h5 {
            font-size: 0.95rem;
        }

        small {
            font-size: 0.7rem;
            color: var(--color-light);
        }

        .about_icon {
            color: var(--color-primary);
            font-size: 1.4rem;
            margin-bottom: 1rem;
        }
    }
}