header {


    height: 100vh;
    padding-top: 4rem;
    overflow: hidden;

    .header_container {
        text-align: center;
        height: 100%;
        position: relative;

        .me {
            background: linear-gradient(var(--color-primary), transparent);
            width: 22rem;
            height: 30rem;
            position: absolute;
            left: calc(50% - 11rem);
            margin-top: 4rem;
            border-radius: 12rem 12rem 0 0;
            overflow: hidden;
            padding-top: 1rem;
        }

        .scroll_down {
            position: absolute;
            right: -2.3rem;
            bottom: 5rem;
            transform: rotate(90deg);
            font-weight: 200;
            font-size: 0.9rem;
        }
    }

    @media (max-width: 1024px) {
        height: 85vh;
        // height: 100vh;
    }

    @media (max-width: 600px) {
        height: 100vh;

        .header_socials,
        .scroll_down {
            display: none;
        }
    }
}

.cta {
    margin-top: 2.5rem;
    gap: 1.2rem;
}

.header_socials {
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;

    &::after {
        content: '';
        width: 1px;
        height: 2rem;
        background: var(--color-primary);
    }
}